export default {
	props: {
		title: {type: String, default: '支付成功'}
	},
	name: '',
	data () {
		return {
			newTitle: this.title
		}
	},
	onLoad (options) {
		let that = this;
		that.newTitle = options.title || '支付成功';
		uni.setNavigationBarTitle({
				title: that.newTitle || '支付成功'
		});
	},
	methods: {
		onConfirmEvent () {
			uni.navigateBack()
		}
	}
}
